.movies__container {
  padding-left: 15px;
  padding-right: 15px;

  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: 40px;

  @media screen and (min-width: $mobile) {
    max-width: 320px;
  }

  @media screen and (min-width: $tablet) {
    max-width: 768px;
    margin-top: 60px;
    margin-bottom: 50px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 1024px;
  }
}
